import { useState } from "react";
import "./styles.css";

const bookf = {
  fiction: [
    {
      name: "the alchemist",
      rating: "1/5"
    },
    { name: "harry potter", rating: "2/5" },
    { name: "oliver twist", rating: "3/5" }
  ],
  politics: [
    { name: "the prince", rating: "4/5" },
    { name: " the republic", rating: "3/5" },
    { name: "arthashastra", rating: "3/5" }
  ],
  selfhelp: [
    { name: "ikigai", rating: "1/5" },
    { name: "rich dad poor dad", rating: "1.5/5" },
    { name: "atomic habits", rating: "2/5 " }
  ]
};
const bookGenre = Object.keys(bookf);
console.log(bookGenre);

export default function App() {
  const [chooseGenre, setGenre] = useState("fiction");
  function genreButtonHandler(genre) {
    setGenre(genre);
  }
  return (
    <div className="App">
      <h1>BOOK RECOMMENDATION</h1>
      <p>These are some of the books on various topics.</p>
      {bookGenre.map((genre) => {
        return (
          <button onClick={() => genreButtonHandler(genre)} keys={genre}>
            {genre}
          </button>
        );
      })}
      <hr />
      <div>
        <ul>
          {bookf[chooseGenre].map((genre) => {
            return (
              <li>
                <div className="name">{genre.name}</div>
                <div className="count">{genre.rating}</div>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}
